











import { getUserInfo } from '@/request/API/index.js';
export default {
	data() {
		return {
			title: 'Hello'
		};
	},
	onLoad() {},
	methods: {
		tuichu() {
			uni.showModal({
				title: '',
				content: '是否确认退出',
				success: res => {
					if (res.confirm) {
						this.$store.commit('SET_SHOPID', '');
						this.$store.commit('SET_USERID', '');
						uni.setStorageSync('userId', '');
						uni.setStorageSync('shopId', '');
						uni.reLaunch({
							url: '/pages/logn/logn'
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	}
};
